@import "./sass-variables.sass";

.albumList {
    position: relative;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    width: 80%;
    margin: auto;
    margin-top: 60px;
    z-index: 2;
    .blob {
        position: absolute; }
    .wave {
        position: absolute; }
    .wave2 {
        position: absolute; }
    #blob5 {
        top: 120px;
        right: -500px; }
    #albumListWave1 {
        top: -150px;
        left: -200px;
        z-index: -10; }
    #albumListWave2 {
        top: 235px;
        left: 70px; }
    #albumListWave3 {
        top: 380px;
        left: -280px; } }
.info {
    color: #003399;
    font-size: 20px;
    font-weight: bold;
    width: 300px;
    min-width: 300px;
    margin: 0 70px 20px 70px;
    border-radius: 20px;
    background-color: white;
    text-align: center;

    a.active {
        color: #3399FF; } }

.search {
    display: flex;
    justify-content: space-around;
    padding: 10px 20px 10px 20px;
    span {
        border: 0;
        background-color: white;
        color: #003399;
        font-size: 30px;
        cursor: pointer;
        outline: none;
        &:hover {
            color: #3399FF; } } }

#albumSites {
    position: relative;
    display: flex;
    place-items: center;
    justify-content: center;
    width: 100%;
    margin-top: 45px;
    z-index: 5;
    span {
        transform: translateY(-5px);
        margin: 0 25px;
        color: rgba($cfifth, 0.48);
        font-size: 60px;
        font-weight: 700;
        -webkit-text-stroke-width: 1px;
        -webkit-text-stroke-color: $cfourth;
        cursor: pointer;
        &:hover {
            color: $clight; } }
    ul {
        display: flex;
        margin: 0px;
        padding: 0px;
        list-style: none;
        li {
            margin: 0 25px;
            color: rgba($csix, 0.5);
            font-size: 24px;
            cursor: pointer;
            &:hover {
                color: rgba($csix, 1); } }
        .active {
            cursor: default;
            pointer-events: none;
            color: rgba($csix, 1); } } }
@media(max-width: 1100px) {
    .info {
        a.active {} }

    #albumSites {
        ul {
            li {
                margin: 0 15px; } } } }
@media(max-width: 650px) {
    .albumList {
        margin-top: 30px; }
    .info {
        a.active {} }
    #albumSites {
        justify-content: center;
        width: 90%;
        margin-left: 20px;
        span {
            margin: 0 10px;
            font-size: 40px; }
        ul {
            justify-content: center;
            flex-wrap: wrap;
            li {
                margin: 0 10px;
                font-size: 16px; } } } }
